import Image from "gatsby-image"

import { rhythm } from "../utils/typography"

function SocialLink({ link, children }) {
  return (
    <a
      style={{
        color: `#757575`,
      }}
      href={link}
      target={`_blank`}
    >
      {children}
    </a>
  )
}

